import React, {Component} from 'react'

import ItemList from './Components/ItemList/'
import ButtonAdd from './Components/Button/'
import InputSearch from './Components/InputSearch/'
import InputBlock from './Components/InputBlock/'
import './style.css'


class ToDo extends Component {

	state = {
		list: [],
		valueInput: '',
		valueText: '',
		isOpen:false,
		list2: [],
		isOpenMessage:true
	}

	addItem = () => {		

		if (this.state.valueInput) {
			let val = {
				title: this.state.valueInput,
				description: this.state.valueText
			}
			this.setState({
				list: [...this.state.list, val],
				valueInput: '',
				valueText: '',
			})

	        this.setState({
				list: [...this.state.list, val],
				valueInput: '',
				valueText: '',
			}, () => {
	            this.setState({list2: this.state.list})
	        })
		}
	}

	ChangeInput = (e) => {
		this.setState({
			valueText: e.target.value
		})
	}

	handleChangeInput = (e) => {
		this.setState({
			valueInput: e.target.value
		})
	}

	toggleOpen = (e) => {
		this.setState({
			isOpen: !this.state.isOpen
		})
	}

	filterList =(e) => {
		this.setState({
	        list2:  this.state.list.filter(item => item.title.includes(e.target.value.toLowerCase()))
	    })
    }

    showMessage() {
    	if (this.state.list.length === 0) {
    		return (
    			<div className='mes'>
    				<p>Записей пока нет :(</p>
    			</div>    			
    		)
    	}
    }

	getInput() {
		if (!this.state.isOpen) return null
		return <InputBlock
			changeInput={this.handleChangeInput}
			valueIn={this.state.valueInput}
			changeText={this.ChangeInput}
			valueText={this.state.valueText}
			clikBut={this.addItem}
		/>
	}

	render () {
		
		return (
			<div>
				<InputSearch filt={this.filterList} />
				{this.showMessage()}
				<ul>					
					{this.state.list2.map((item, index) => (
						<ItemList 
							key={index}
							title={item.title}
							description={item.description}
							onDelete ={ () => {									
								this.state.list.map((item2, index2) => {
									if (item2 === item) {
										this.state.list.splice(index2, 1);
										this.setState({list: this.state.list});
									}
								})	
								this.state.list2.splice(index, 1);
								this.setState({list2: this.state.list2});								
							}}
						/>
					))}					
				</ul>
				<ButtonAdd onToggle={this.toggleOpen} />
				{this.getInput()}								
			</div>
		)
	}
}

export default ToDo
